.backdrop {
  position: absolute;
  width: 100%;
  height: 100vh;
  background: hsl(0deg, 0%, 0%);
  opacity: 0.5;
  display: none;
  transition: all 0.3s ease-in-out;
  z-index: 7;
}
.backdrop.nav-visible {
  display: block;
}
@media (min-width: 71.875em) {
  .backdrop.nav-visible {
    display: none;
  }
}
@media (min-width: 71.875em) {
  .backdrop {
    display: none;
  }
}

.header-buttons {
  display: flex;
  width: auto;
  height: 100%;
  align-items: center;
  padding-left: 1.875rem;
  flex-grow: 1;
}
.header-buttons .header-buttons__logo {
  display: block;
  flex-grow: 1;
}
@media (min-width: 71.875em) {
  .header-buttons .header-buttons__logo {
    flex-grow: unset;
  }
}
.header-buttons .header-buttons__nav-trigger {
  padding-right: 1.25rem;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9;
}
@media (min-width: 71.875em) {
  .header-buttons .header-buttons__nav-trigger {
    display: none;
  }
}
.header-buttons .header-buttons__nav-trigger span {
  min-width: 1.25rem;
  height: 3.125rem;
  background-repeat: no-repeat;
  background-position: center;
  transition: all 0.3s ease-in-out;
  background-image: url("../images/icon-menu.svg");
}
.header-buttons .header-buttons__nav-trigger span.nav-visible {
  background-image: url("../images/icon-close.svg");
}

.header-options {
  display: flex;
  align-items: center;
  padding-right: 1.875rem;
  position: relative;
}
.header-options .header-options__cart {
  padding: 0 1.25rem;
  transition: all 0.3s ease-in-out;
}
.header-options .header-options__cart:hover #cart path, .header-options .header-options__cart.cart-visible #cart path {
  fill: hsl(26deg, 100%, 55%);
  transition: all 0.3s ease-in-out;
}
.header-options .header-options__user {
  width: 3.125rem;
  height: 3.125rem;
  border-radius: 100%;
  border: 0.125rem solid transparent;
  transition: all 0.3s ease-in-out;
}
.header-options .header-options__user:hover {
  border-color: hsl(26deg, 100%, 55%);
}
.header-options .header-options__user img {
  width: 100%;
}

nav.main-nav {
  width: 16.25rem;
  height: 100vh;
  background: hsl(0deg, 0%, 100%);
  border-right: 1px solid #b6bcc8;
  display: flex;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 8;
  transform: translateX(-260px);
  padding-top: 5rem;
  transition: all 0.3s ease-in-out;
}
@media (min-width: 71.875em) {
  nav.main-nav {
    background: transparent;
  }
}
nav.main-nav.nav-visible {
  transform: translateX(0);
}
@media (min-width: 71.875em) {
  nav.main-nav {
    width: 100%;
    height: 100%;
    position: relative;
    padding-top: 0;
    transform: translateX(0);
    border: none;
  }
}

ul.main-nav__list {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}
@media (min-width: 71.875em) {
  ul.main-nav__list {
    flex-direction: row;
    padding-left: 1.875rem;
  }
}
ul.main-nav__list li {
  width: auto;
  height: 3.125rem;
}
ul.main-nav__list li a {
  display: flex;
  color: hsl(0deg, 0%, 0%);
  height: 100%;
  padding: 0 1.5625rem;
  font-weight: bold;
  align-items: center;
}
@media (min-width: 71.875em) {
  ul.main-nav__list li a {
    color: hsl(219deg, 9%, 45%);
    font-weight: normal;
    border-bottom: 0.125rem solid transparent;
  }
}
ul.main-nav__list li a:hover, ul.main-nav__list li a:active, ul.main-nav__list li a:focus {
  color: hsl(26deg, 100%, 55%);
}
@media (min-width: 71.875em) {
  ul.main-nav__list li a:hover, ul.main-nav__list li a:active, ul.main-nav__list li a:focus {
    color: hsl(0deg, 0%, 0%);
    border-bottom-color: hsl(26deg, 100%, 55%);
  }
}
@media (min-width: 71.875em) {
  ul.main-nav__list li {
    height: 100%;
  }
}